'use client';

import { useState, useEffect, useMemo, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogClose } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { Plus, CalendarIcon, X, Loader, Flag, Clock, User } from 'lucide-react';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';
import { createSubTask } from '@/modules/project-management/task/slices/subTaskSlice';
import { toast } from 'sonner';
import { useCurrentUser } from '@/hooks/useCurrentUser';

const CreateSubtaskModal = ({ projectId, taskDetails, open, setOpen, taskId }) => {
  const { currentUser } = useCurrentUser();
  const dispatch = useDispatch();
  const { loading: subTaskLoading, error: subTaskError } = useSelector((state) => state.subTask);

  // Normalize assignees list (array of objects)
  const assigneesList = useMemo(() => {
    if (!taskDetails?.assignedTo) return [];
    if (Array.isArray(taskDetails.assignedTo)) return taskDetails.assignedTo;
    return [taskDetails.assignedTo]; // if single object
  }, [taskDetails?.assignedTo]);

  // Selected full assignee object (not just id/name)
  const [selectedAssignee, setSelectedAssignee] = useState(assigneesList[0] || null);

  const initialFormData = useMemo(() => ({
    title: '',
    priority: 'Medium',
    description: '',
    assignedBy: currentUser?.name || 'Current User',
    projectId: taskDetails?.projectId || projectId || '',
  }), [currentUser?.name, taskDetails, projectId]);

  const [formData, setFormData] = useState(initialFormData);
  const [errors, setErrors] = useState({});
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedTime, setSelectedTime] = useState('');

  useEffect(() => {
    if (open) {
      setFormData(initialFormData);
      setSelectedAssignee(assigneesList[0] || null);
      setErrors({});
      setSelectedDate(null);
      setSelectedTime('');
    }
  }, [open, initialFormData, assigneesList]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    setErrors(prev => ({ ...prev, [name]: '' }));
  };

  const handlePriorityChange = (value) => {
    setFormData(prev => ({ ...prev, priority: value }));
  };

  // When user picks a member â†’ store full object
  const handleAssigneeChange = (memberId) => {
    const member = assigneesList.find(m => m.memberId === memberId || m._id === memberId);
    setSelectedAssignee(member || null);
    setErrors(prev => ({ ...prev, assignedTo: '' }));
  };

  const validate = () => {
    const newErrors = {};
    if (!formData.title.trim()) newErrors.title = 'Title is required';
    if (!formData.priority) newErrors.priority = 'Priority is required';
    if (!selectedDate) newErrors.deadline = 'Deadline is required';
    if (!formData.description.trim()) newErrors.description = 'Description is required';
    if (!selectedAssignee) newErrors.assignedTo = 'Please select assignee';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleAdd = async () => {
    if (!validate()) {
      toast.error('Please fill all required fields');
      return;
    }

    const pad = n => (n < 10 ? '0' + n : n);
    let deadline = null;
    if (selectedDate) {
      const d = new Date(selectedDate);
      const [h = 23, m = 59] = selectedTime ? selectedTime.split(':').map(Number) : [];
      d.setHours(h, m, 0, 0);
      deadline = `${d.getFullYear()}-${pad(d.getMonth() + 1)}-${pad(d.getDate())}T${pad(d.getHours())}:${pad(d.getMinutes())}:00`;
    }

    // THIS IS THE KEY PART â€” Send FULL assignee object
    const subTaskData = {
      title: formData.title.trim(),
      priority: formData.priority,
      deadline,
      description: formData.description.trim(),
      assignedTo: selectedAssignee ? {                     // â† Full object sent!
        memberId: selectedAssignee.memberId,
        memberName: selectedAssignee.memberName,
        role: selectedAssignee.role,
        _id: selectedAssignee._id
      } : null,
      assignedBy: formData.assignedBy,
      projectId: formData.projectId,
      task_id: taskDetails?.task_id || taskId,
      projectName: taskDetails?.projectName,
    };

    try {
      await dispatch(createSubTask({ taskId, subTaskData })).unwrap();
      toast.success('Subtask created successfully');
      setOpen(false);
    } catch (err) {
      toast.error(subTaskError || 'Failed to create subtask');
    }
  };

  const isButtonEnabled = formData.title.trim() && formData.priority && selectedDate && formData.description.trim() && selectedAssignee;

  const displayDateTime = selectedDate
    ? format(selectedDate, 'MMM dd, yyyy') + (selectedTime ? ` at ${selectedTime}` : ' (End of Day)')
    : '';

  if (!open) return null;

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader className="bg-gradient-to-r from-blue-50 to-indigo-50">
          <DialogTitle className="flex items-center text-lg font-bold">
            <Plus className="mr-2 h-5 w-5 text-blue-600" />
            Add New Subtask
          </DialogTitle>
          <DialogClose asChild>
            <Button variant="ghost" size="icon"><X /></Button>
          </DialogClose>
        </DialogHeader>

        <div className="p-6">
          <form onSubmit={(e) => { e.preventDefault(); handleAdd(); }} className="space-y-5">

            {/* Title */}
            <div>
              <label className="flex items-center gap-2 font-medium"><Plus className="h-4 w-4 text-blue-600" /> Subtask Title <span className="text-red-500">*</span></label>
              <Textarea name="title" value={formData.title} onChange={handleChange} placeholder="Enter title" className="mt-2" />
              {errors.title && <p className="text-red-500 text-sm mt-1">{errors.title}</p>}
            </div>

            {/* Priority + Assignee */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="flex items-center gap-2 font-medium"><Flag className="h-4 w-4 text-blue-600" /> Priority <span className="text-red-500">*</span></label>
                <Select value={formData.priority} onValueChange={handlePriorityChange}>
                  <SelectTrigger className="mt-2"><SelectValue /></SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Low">Low</SelectItem>
                    <SelectItem value="Medium">Medium</SelectItem>
                    <SelectItem value="High">High</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              {/* Assignee Dropdown */}
              <div>
                <label className="flex items-center gap-2 font-medium"><User className="h-4 w-4 text-blue-600" /> Assign To <span className="text-red-500">*</span></label>
                <Select
                  value={selectedAssignee?.memberId || ''}
                  onValueChange={handleAssigneeChange}
                  disabled={assigneesList.length === 0}
                >
                  <SelectTrigger className="mt-2">
                    <SelectValue placeholder={assigneesList.length === 0 ? "No members" : "Select member"} />
                  </SelectTrigger>
                  <SelectContent>
                    {assigneesList.map((member) => (
                      <SelectItem key={member._id} value={member.memberId}>
                        {member.memberName} ({member.role})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                {errors.assignedTo && <p className="text-red-500 text-sm mt-1">{errors.assignedTo}</p>}
              </div>
            </div>

            {/* Deadline + Time */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="flex items-center gap-2 font-medium"><CalendarIcon className="h-4 w-4 text-blue-600" /> Deadline <span className="text-red-500">*</span></label>
                <Popover>
                  <PopoverTrigger asChild>
                    <Button variant="outline" className="w-full mt-2 justify-start">
                      {displayDateTime || "Pick date"}
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-auto p-0">
                    <Calendar mode="single" selected={selectedDate} onSelect={setSelectedDate} />
                  </PopoverContent>
                </Popover>
              </div>
              <div>
                <label className="flex items-center gap-2 font-medium"><Clock className="h-4 w-4 text-blue-600" /> Time (Optional)</label>
                <Input type="time" value={selectedTime} onChange={(e) => setSelectedTime(e.target.value)} className="mt-2" />
              </div>
            </div>

            {/* Description */}
            <div>
              <label className="font-medium">Description <span className="text-red-500">*</span></label>
              <Textarea name="description" value={formData.description} onChange={handleChange} placeholder="Details..." className="mt-2 min-h-32" />
              {errors.description && <p className="text-red-500 text-sm mt-1">{errors.description}</p>}
            </div>

            {/* Buttons */}
            <div className="flex justify-end gap-3 pt-4 border-t">
              <Button type="button" variant="outline" onClick={() => setOpen(false)}>Cancel</Button>
              <Button
                type="submit"
                disabled={!isButtonEnabled || subTaskLoading}
                className="bg-blue-600 hover:bg-blue-700"
              >
                {subTaskLoading ? <> <Loader className="h-4 w-4 animate-spin mr-2" /> Creating...</> : 'Create Subtask'}
              </Button>
            </div>
          </form>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default CreateSubtaskModal;